r\n@mixin aspect-ratio-fullwidth($width, $height, $heightType) {\r\n  position: relative;\r\n  overflow: hidden;\r\n  width: 100%;\r\n\r\n  @if $heightType==\"min-height\" {\r\n    min-height: calc(100vw * ( #{$width} / #{$height} ));\r\n  }\r\n\r\n  @else if $heightType==\"height\" {\r\n    height: calc(100vw * ( #{$width} / #{$height} ));\r\n  }\r\n\r\n  > img,\r\n  > video,\r\n  > figure,\r\n  > iframe {\r\n    position: absolute;\r\n    top: 0;\r\n    left: -9999px;\r\n    right: -9999px;\r\n    bottom: 0;\r\n    min-height: 100%;\r\n    min-width: 100%;\r\n    height: 100%;\r\n    width: 100%;\r\n    margin: auto; //center left, right\r\n    object-fit: cover; //act like background-size: cover if supported\r\n  }\r\n}\r\n\r\n// Define vertical, horizontal, or both position\r\n@mixin center($position) {\r\n  position: absolute;\r\n\r\n  @if $position==\"vertical\" {\r\n    top: 50%;\r\n    -webkit-transform: translateY(-50%);\r\n    -ms-transform: translateY(-50%);\r\n    transform: translateY(-50%);\r\n    -moz-transform: translateY(-50%);\r\n    -o-transform: translateY(-50%);\r\n  }\r\n\r\n  @else if $position==\"horizontal\" {\r\n    left: 50%;\r\n    -webkit-transform: translateX(-50%);\r\n    -ms-transform: translateX(-50%);\r\n    transform: translate(-50%);\r\n  }\r\n\r\n  @else if $position==\"both\" {\r\n    top: 50%;\r\n    left: 50%;\r\n    -webkit-transform: translate(-50%, -50%);\r\n    -ms-transform: translate(-50%, -50%);\r\n    transform: translate(-50%, -50%);\r\n  }\r\n}\r\n\r\n//add ellipsis\r\n@mixin truncate($truncation-boundary) {\r\n  max-width: $truncation-boundary;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n@mixin center-block {\r\n  display: block;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n}\r\n\r\n//mixins for font calculation\r\n@mixin calcRem($size) {\r\n  font-size: ($size / 16px) * 1rem;\r\n}\r\n\r\n@mixin background-gradient($start-color, $end-color, $orientation) {\r\n  b